  float: right;
  font-size: 36px;
  font-weight: bold;
  color: grey;
  margin-top: -20px;
}
.menu__header span:hover {
  color: #4d5454;
  cursor: pointer;
}

.list-block {
  display: flex;
  flex-direction: column;
  padding: 25px 0 5px 0;
  border-bottom: solid 1px #c8c8c1;
}
.list-block__item {
  padding-bottom: 19px;
}
.list-block__item a {
  text-decoration: none;
  font-family: "Open Sans";
  font-size: 16px;
  line-height: 24px;
  color: black;
}
.list-block__item a:hover {
