}

@media (max-width: 767px) {
  .modal__window {
    width: 290px;
    margin: 30% auto;
  }
}
.header__menu a {
  font-family: "Open Sans";
  text-decoration: none;
  font-size: 14px;
  color: black;
  margin-left: 29px;
}
.header__menu a:hover {
  color: #ef5b4c;
}
.header__menu a.header__menu-active {
