.fileinput-err label, .fileinput-fill label, .fileinput-active label, .fileinput label {
  width: 452px;
  height: 28px;
  display: block;
  border-radius: 3px;
  vertical-align: center;
  padding: 8px 0 0 14px;
  font-family: "Open Sans";
  font-size: 14px;
  cursor: pointer;
}

