  }
}
.fileinput-err__button, .fileinput-fill__button, .fileinput-active__button, .fileinput__button {
  float: right;
  overflow: hidden;
  padding: 9px 15px 8px 15px;
  margin: -8px -0.5px 0 0;
  font-family: "Open Sans";
  font-size: 14px;
  color: black;
  background-color: lightgray;
}

.err-fileinput span, .err-input span {
  font-size: 17px;
