}

.fileinput-active input[type=file] {
  display: none;
}
.fileinput-active input[type=file]:active {
  display: none;
}
.fileinput-active label {
  border: solid 2px blue;
  color: black;
}
.fileinput-active__button {
  border-left: solid 2px blue;
}

.fileinput-fill input[type=file] {
  display: none;
}
.fileinput-fill label {
  border: solid 1px grey;
